   font-size: 14px;
   font-weight: 400;
   text-transform: uppercase;
   letter-spacing: 0.05em;
   color: #71798f;
   margin-bottom: 12px;
 }
 
 .details {
   width: 100%;
 }
 
 .details ul {
   margin: 40px 0px 0px 0px;
   list-style: none;
   padding: 0;
   display: grid;
   grid-template-columns: calc(33.333% - 16px) 66.667%;
   grid-column-gap: 16px;
   align-items: baseline;
 }
 
