 /*FADE IN*/
 .fade {
   opacity: 0;
   transform: translateY(20px);
   transition: opacity 0.75s ease-in-out, transform 0.75s ease-in-out;
 }
 
 .fade.appear {
   opacity: 1;
   transform: translateY(0px);
 }
 
 /*MODALS*/
 .modal {
   position: fixed;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   display: none;
   justify-content: center;
   align-items: center;
   background-color: rgba(0, 0, 0, 0.25);
   z-index: 15;
 }
 
 .modal img {
   width: auto;
   height: auto;
   max-width: calc(83.333% - 64px);
   max-height: 80%;
   object-fit: contain;
 }
 
